import * as React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';

import { alpha, styled } from '@mui/material/styles';
import TextField from '@mui/material/TextField';
import Checkbox from '@mui/material/Checkbox';
import Tooltip from '@mui/material/Tooltip';
import TableCell from '@mui/material/TableCell';
import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import TableSortLabel from '@mui/material/TableSortLabel';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import IconButton from '@mui/material/IconButton';
import Avatar from '@mui/material/Avatar';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import FilterListIcon from '@mui/icons-material/FilterList';
import FilterListOffIcon from '@mui/icons-material/FilterListOff';
import ContentCopy from '@mui/icons-material/ContentCopy';
import AccessTime from '@mui/icons-material/AccessTime';
import { visuallyHidden } from '@mui/utils';
import { green, grey, orange } from '@mui/material/colors';

import SetWillBeAvailableDialog from './setWillBeAvailable';
import { ReactComponent as CatIcon } from '../icons/cat.svg';
import { ReactComponent as TruckIcon } from '../icons/truck.svg';

import Loader from '../components/Loader';
import LocationForGrid from '../components/LocationForGrid';
import { TrucksQuery, TruckDelete } from '../api';
import { useMap, useSnackbar } from '../hooks';
import { Order, Truck } from '../utils/dataTypes';
import { getPath, getType } from '../utils/queryHelpers';
import { stopPropagation } from '../utils/stopPropagation';

interface HeadCell {
  id: string;
  disablePadding: boolean;
  orderField?: keyof Truck;
  getData: (
    row: Truck,
    setMapData: any,
  ) => { data: JSX.Element | string; title?: string };
  label: string;
  numeric: boolean;
}
const themeMode = localStorage.getItem('theme') === 'dark' ? 'dark' : 'light';
const headCells: readonly HeadCell[] = [
  {
    id: 'truckNumber',
    orderField: 'truckNumber',
    getData: (row) => ({ data: `${row.truckNumber}` }),
    numeric: true,
    disablePadding: false,
    label: 'Truck Number',
  },
  {
    id: 'mainContactInfo',
    getData: (row) => {
      const person = row.coordinator || row.owner;
      let path = '';
      let type = '';
      let firstRow = '';
      let secondRow = '';
      if (person) {
        path = `./${getPath(person)}/view/${person.id}`;
        type = `${getType(person)} `;
        firstRow = `${person.fullName}`;
        secondRow = `${person?.phone}`;
      }
      return {
        data: (
          <span style={{ display: 'flex' }}>
            <span
              style={{
                textOverflow: 'ellipsis',
                overflow: 'hidden',
                textAlign: 'left',
              }}
            >
              <b>{type}</b>
              <a href={path} target="_blank" onClick={stopPropagation}>
                {firstRow}
              </a>
              <br />
              <a
                href={secondRow ? `tel:${secondRow}` : undefined}
                target="_blank"
                onClick={stopPropagation}
              >
                {secondRow || ' '}
              </a>
            </span>
            <IconButton
              size="small"
              onClick={(event) => {
                stopPropagation(event);
                navigator.clipboard.writeText(firstRow + ' ' + secondRow);
              }}
            >
              <ContentCopy sx={{ height: '16px', width: '16px' }} />
            </IconButton>
          </span>
        ),
        title: type + '' + firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Main Contact info',
  },
  {
    id: 'status',
    orderField: 'status',
    getData: (row) => {
      const status = `${row.status}`;
      const val = (() => {
        switch (status) {
          case 'Will be available':
            return (
              <CatIcon
                style={{
                  height: '24px',
                  width: '24px',
                  color: themeMode === 'dark' ? grey[100] : grey[900],
                }}
              />
              /*<AccessTime
                sx={{ height: '24px', width: '24px', color: grey[900] }}
              />*/
            );
          case 'On route':
            return (
              <TruckIcon
                style={{
                  height: '24px',
                  width: '24px',
                  color: themeMode === 'dark' ? grey[300] : grey[700],
                }}
              />
              /*<LocalShipping
                sx={{ height: '24px', width: '24px', color: orange[500] }}
              />*/
            );
          case 'Available':
            return (
              <Avatar
                sx={{
                  height: '24px',
                  width: '24px',
                  bgcolor: green[500],
                  margin: 'auto',
                }}
              >
                A
              </Avatar>
            );
          case 'Not Available':
            return (
              <Avatar
                sx={{
                  height: '24px',
                  width: '24px',
                  bgcolor: grey[500],
                  margin: 'auto',
                }}
              >
                N
              </Avatar>
            );
          default:
            return status;
        }
      })();

      return {
        data: <span>{val}</span>,
        title: status,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Status',
  },
  {
    id: 'location',
    getData: (row, setMapData) => {
      const onRefClick = (event: React.SyntheticEvent) => {
        event.preventDefault();
        stopPropagation(event);
        setMapData([row]);
      };

      if (row?.lastLocation) {
        const secondRow = `${
          row.locationUpdatedAt
            ? new Date(row.locationUpdatedAt).toLocaleString()
            : ''
        }`;

        return {
          data: (
            <LocationForGrid
              location={row.lastLocation}
              onRefClick={onRefClick}
              addInfo={secondRow}
            />
          ),
        };
      } else {
        return { data: '' };
      }
    },
    numeric: false,
    disablePadding: true,
    label: 'Last known location',
  },
  {
    id: 'truckInfo',
    getData: (row) => {
      const firstRow = row.type ? `${row.type}` : '';
      const secondRow = row.equipment ? `${row.equipment.join(', ')}` : '';

      return {
        data: (
          <span>
            {firstRow}
            <br />
            {secondRow}
          </span>
        ),
        title: firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Truck info',
  },
  {
    id: 'dimsAndPayload',
    getData: (row) => {
      const firstRowPart1 = row.insideDims ? `${row.insideDims}` : '';
      const firstRowPart2 = row.doorDims ? `${row.doorDims}` : '';
      const secondRow = row.payload ? `${row.payload}` : '';

      return {
        data: (
          <span>
            {(firstRowPart1 ? firstRowPart1 + ' ' : '') +
              `${firstRowPart2 ? '(' + firstRowPart2 + ')' : ''}`}
            <br />
            {secondRow}
          </span>
        ),
        title:
          (firstRowPart1 ? firstRowPart1 + ' Inside ' : '') +
          (firstRowPart2 ? firstRowPart2 + ' Door ' : '') +
          '\n' +
          secondRow +
          ' Payload',
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'DIMS/Payload',
  },
  {
    id: 'driverInfo',
    getData: (row) => {
      const driver = row.driver;
      let path = '';
      let type = '';
      let firstRow = '';
      let secondRow = '';
      if (driver) {
        path = `./${getPath(driver)}/view/${driver.id}`;
        type = `${getType(driver)} `;
        firstRow = `${driver.fullName}`;
        secondRow = `${driver.phone}`;
      }
      return {
        data: (
          <span>
            <b>{type}</b>
            <a
              href={firstRow ? path : undefined}
              target="_blank"
              onClick={stopPropagation}
            >
              {firstRow}
            </a>
            <br />
            <a
              href={secondRow ? `tel:${secondRow}` : undefined}
              target="_blank"
              onClick={stopPropagation}
            >
              {secondRow || ' '}
            </a>
          </span>
        ),
        title: type + '' + firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Driver info',
  },
];

interface EnhancedTableProps {
  numSelected: number;
  onRequestSort: (
    event: React.MouseEvent<unknown>,
    property: keyof Truck,
  ) => void;
  onSelectAllClick: (event: React.ChangeEvent<HTMLInputElement>) => void;
  order: Order | undefined;
  orderBy: string | undefined;
  rowCount: number;
}

const StyledTableHead = styled(TableHead)(({ theme }) => ({
  backgroundColor: theme.palette.background.default,
}));

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  maxWidth: '100px',
  paddingTop: '6px',
  paddingBottom: '6px',
  whiteSpace: 'nowrap',
  textOverflow: 'ellipsis',
  overflow: 'hidden',
}));

function EnhancedTableHead(props: EnhancedTableProps) {
  const {
    onSelectAllClick,
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort,
  } = props;
  const createSortHandler =
    (property: keyof Truck) => (event: React.MouseEvent<unknown>) => {
      onRequestSort(event, property);
    };

  return (
    <StyledTableHead>
      <TableRow>
        <StyledTableCell padding="checkbox">
          <Checkbox
            name={'enhanced-table-checkbox-select-all'}
            color="primary"
            indeterminate={numSelected > 0 && numSelected < rowCount}
            checked={rowCount > 0 && numSelected === rowCount}
            onChange={onSelectAllClick}
            inputProps={{
              'aria-label': 'enhanced-table-checkbox-select-all',
            }}
          />
        </StyledTableCell>
        {headCells.map((headCell) => (
          <StyledTableCell
            key={headCell.id}
            align={headCell.numeric ? 'right' : 'center'}
            padding={headCell.disablePadding ? 'none' : 'normal'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={headCell.orderField && orderBy === headCell.orderField}
              hideSortIcon={!headCell.orderField}
              direction={orderBy === headCell.orderField ? order : 'asc'}
              onClick={
                headCell.orderField
                  ? createSortHandler(headCell.orderField)
                  : () => {
                      return;
                    }
              }
            >
              {headCell.label}
              {headCell.orderField && orderBy === headCell.orderField ? (
                <Box component="span" sx={visuallyHidden}>
                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                </Box>
              ) : null}
            </TableSortLabel>
          </StyledTableCell>
        ))}
      </TableRow>
    </StyledTableHead>
  );
}

interface EnhancedTableToolbarProps {
  selected: readonly string[];
  setQueryParams: React.Dispatch<React.SetStateAction<Record<string, number>>>;
}

function EnhancedTableToolbar(props: EnhancedTableToolbarProps) {
  const { selected, setQueryParams } = props;
  const [truckId, setTruckId] = React.useState<string>('');
  const [truckNumberSearch, setTruckNumberSearch] = React.useState<number>(0);
  const [truckNumberChanged, setTruckNumberChanged] =
    React.useState<boolean>(false);
  const location = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();

  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(TruckDelete, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Deleted',
      });
      queryClient.invalidateQueries(['trucks']);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const truckNumberHandler = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (Number.isFinite(Number(event.target.value))) {
      setTruckNumberSearch(Number(event.target.value));
      setTruckNumberChanged(true);
    }
  };
  const filterClearHandler = () => {
    setTruckNumberChanged(false);
    setTruckNumberSearch(0);
    setQueryParams({});
  };
  const filterHandler = () => {
    setTruckNumberChanged(false);
    setQueryParams({ truckNumber: truckNumberSearch });
  };
  const keyUpHandler = (event: React.KeyboardEvent) => {
    if (event.key !== 'Enter') {
      return;
    }
    if (truckNumberSearch) {
      filterHandler();
    } else {
      filterClearHandler();
    }
  };
  const openWBADialogHandler = () => {
    setTruckId(selected[0]);
  };
  const createHandler = () => {
    navigate('./create', {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };
  const editHandler = () => {
    navigate(`./edit/${selected[0]}`, {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };
  const deleteHandler = () => {
    mutate(`${selected[0]}`);
  };

  return (
    <Toolbar
      sx={{
        overflow: 'hidden',
        pl: { sm: 2 },
        pr: { xs: 1, sm: 1 },
        ...(selected.length > 0 && {
          bgcolor: (theme) =>
            alpha(
              theme.palette.primary.main,
              theme.palette.action.activatedOpacity,
            ),
        }),
      }}
    >
      {selected.length > 0 ? (
        <Typography
          sx={{ flex: '1 1 100%' }}
          color="inherit"
          variant="subtitle1"
          component="div"
        >
          {selected.length} selected
        </Typography>
      ) : (
        <Typography
          sx={{
            flex: '1 1 100%',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            overflow: 'hidden',
          }}
          variant="h6"
          id="tableTitle"
          component="div"
        >
          Trucks
        </Typography>
      )}
      <TextField
        label="Truck Number"
        value={truckNumberSearch || ''}
        onChange={truckNumberHandler}
        onKeyUp={keyUpHandler}
        size="small"
      />
      <Tooltip title="Filter list">
        {(truckNumberChanged || !truckNumberSearch) &&
        !(truckNumberChanged && !truckNumberSearch) ? (
          <span>
            <IconButton disabled={!truckNumberSearch} onClick={filterHandler}>
              <FilterListIcon />
            </IconButton>
          </span>
        ) : (
          <span>
            <IconButton onClick={filterClearHandler}>
              <FilterListOffIcon />
            </IconButton>
          </span>
        )}
      </Tooltip>
      <Box sx={{ p: 1, display: 'inline-flex' }}>
        {selected.length === 1 ? (
          <>
            <Tooltip title="Set Will be available">
              <span>
                <IconButton onClick={openWBADialogHandler}>
                  <AccessTime />
                </IconButton>
              </span>
            </Tooltip>
            <SetWillBeAvailableDialog
              truckId={truckId}
              setTruckId={setTruckId}
            />
          </>
        ) : (
          <Tooltip title="Create">
            <span>
              <IconButton onClick={createHandler}>
                <AddIcon />
              </IconButton>
            </span>
          </Tooltip>
        )}
        <Tooltip title="Edit">
          <span>
            <IconButton disabled={selected.length !== 1} onClick={editHandler}>
              <EditIcon />
            </IconButton>
          </span>
        </Tooltip>
        <Tooltip title="Delete">
          <span>
            <IconButton
              disabled={selected.length !== 1}
              onClick={deleteHandler}
            >
              <DeleteIcon />
            </IconButton>
          </span>
        </Tooltip>
      </Box>
      <Loader visible={isLoading} />
    </Toolbar>
  );
}

export default function EnhancedTable() {
  const [order, setOrder] = React.useState<Order | undefined>('asc');
  const [orderBy, setOrderBy] = React.useState<keyof Truck | undefined>(
    'truckNumber',
  );
  const [selected, setSelected] = React.useState<readonly string[]>([]);
  const [page, setPage] = React.useState<number>(0);
  const [rowsPerPage, setRowsPerPage] = React.useState<number>(15);
  const [queryParams, setQueryParams] = React.useState<Record<string, any>>({});
  const tableContainerRef = React.useRef(null);
  const location = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();
  const [, setMapData] = useMap();

  const {
    isLoading,
    isFetching,
    data: { items, total } = { items: [], total: 0 },
  } = useQuery({
    queryKey: ['trucks', page, rowsPerPage, orderBy, order, queryParams],
    queryFn: TrucksQuery,
    keepPreviousData: true,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });
  const rows = items || [];

  React.useEffect(() => {
    if (tableContainerRef.current) {
      const container = tableContainerRef.current as HTMLElement;
      container.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth',
      });
    }
  }, [rows]);

  React.useEffect(() => {
    const newSelected: string[] = [];
    selected.forEach((selectedItem) => {
      const selectedRow = rows.find((row) => row.id === selectedItem);
      if (selectedRow) {
        newSelected.push(selectedItem);
      }
    });
    setSelected(newSelected);
  }, [rows]);

  const handleRequestSort = (
    event: React.MouseEvent<unknown>,
    property: keyof Truck,
  ) => {
    const isAsc = orderBy === property && order === 'asc';
    const isDesc = orderBy === property && order === 'desc';
    if (isDesc) {
      setOrder(undefined);
      setOrderBy(undefined);
    } else {
      setOrder(isAsc ? 'desc' : 'asc');
      setOrderBy(property);
    }
  };

  const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const newSelected = rows.map((n: Truck) => n.id);
      setSelected(newSelected);
      return;
    }
    setSelected([]);
  };

  const handleClick = (
    event: React.MouseEvent<unknown>,
    item: (typeof rows)[number],
  ) => {
    navigate(`./view/${item.id}`, {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };

  const handleSelect = (event: React.MouseEvent<unknown>, name: string) => {
    event.stopPropagation();
    const selectedIndex = selected.indexOf(name);
    let newSelected: readonly string[] = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }

    setSelected(newSelected);
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const isSelected = (name: string) => selected.indexOf(name) !== -1;

  return (
    <Box sx={{ width: '100%', height: '100%' }}>
      <Paper sx={{ width: '100%', height: '100%', backgroundColor: 'unset' }}>
        <EnhancedTableToolbar
          selected={selected}
          setQueryParams={(query) => {
            setQueryParams(query);
            setPage(0);
          }}
        />
        <TableContainer
          ref={tableContainerRef}
          sx={{ height: 'calc(100% - 115px)' }}
        >
          <Table
            sx={{ minWidth: 750 }}
            aria-labelledby="tableTitle"
            size="small"
            stickyHeader
          >
            <EnhancedTableHead
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
            />
            <TableBody>
              {rows.map((row, index) => {
                const isItemSelected = isSelected(`${row.id}`);
                const labelId = `enhanced-table-checkbox-${index}`;
                return (
                  <TableRow
                    hover
                    onDoubleClick={(event) => handleClick(event, row)}
                    role="checkbox"
                    aria-checked={isItemSelected}
                    tabIndex={-1}
                    key={row.id}
                    selected={isItemSelected}
                    sx={{ cursor: 'pointer' }}
                  >
                    <StyledTableCell padding="checkbox">
                      <Checkbox
                        id={`${labelId}`}
                        color="primary"
                        checked={isItemSelected}
                        inputProps={{
                          'aria-labelledby': labelId,
                        }}
                        onClick={(event) => handleSelect(event, `${row.id}`)}
                      />
                    </StyledTableCell>
                    {headCells.map((cell) => {
                      const cellData = cell.getData(row, setMapData);
                      return (
                        <StyledTableCell
                          key={cell.id}
                          title={cellData.title}
                          align={cell.numeric ? 'right' : 'center'}
                        >
                          {cellData.data}
                        </StyledTableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 15, 20, 25, 35, 50]}
          component="div"
          count={total}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
      <Loader visible={isLoading || isFetching} />
    </Box>
  );
}
