import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Dialog from '@mui/material/Dialog';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import { Loader, SearchLocationField, DateTimeField } from '../components';

import { TruckUpdate } from '../api';
import { useSnackbar } from '../hooks';
import { UpdateTruck, TruckStatus } from '../utils/dataTypes';
import { TransitionProps } from '@mui/material/transitions';
import Slide from '@mui/material/Slide';

const validationSchema = yup.object({
  availabilityLocation: yup
    .tuple([
      yup
        .number()
        .typeError('Latitude is required')
        .min(-90, 'Latitude must be grater than or equal: -90')
        .max(90, 'Latitude must be less than or equal: 90')
        .required('Location should be in format "latitude, longitude"'),
      yup
        .number()
        .typeError('Longitude is required')
        .min(-180, 'Longitude must be grater than or equal: -180')
        .max(180, 'Longitude must be less than or equal: 180')
        .required('Location should be in format "latitude, longitude"'),
    ])
    .required('Will be available location is required'),
  availabilityAt: yup
    .date()
    .typeError('Enter correct date')
    .required('Will be available date is required'),
});

const initialValues = {
  id: '',
  status: 'Will be available' as TruckStatus,
  availabilityLocation: null,
  availabilityAt: undefined,
};

const Transition = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>,
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function SetWillBeAvailableDialog({
  truckId,
  setTruckId,
}: {
  truckId: string;
  setTruckId: React.Dispatch<React.SetStateAction<string>>;
}) {
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(TruckUpdate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Will be available set',
      });
      setTruckId('');
      queryClient.invalidateQueries(['trucks']);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<UpdateTruck>({
    initialValues: { ...initialValues, id: truckId },
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values);
    },
  });

  const handleClose = () => {
    setTruckId('');
  };
  const handleDialogClose = () => {
    setTruckId('');
  };

  return (
    <Dialog
      open={!!truckId}
      TransitionComponent={Transition}
      keepMounted
      onClose={handleDialogClose}
      aria-describedby="alert-dialog-set-will-be-available"
      PaperProps={{ sx: { width: '600px' } }}
    >
      <DialogTitle>
        <span>Set "Will ba available"</span>
      </DialogTitle>
      <DialogContent>
        <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
          <Box sx={{ display: 'grid' }}>
            <Box key="controls" sx={{ display: 'inline-flex' }}>
              <Box key="left" sx={{ p: 1, width: '50%' }}>
                <SearchLocationField
                  fieldName="availabilityLocation"
                  label="Will be available at location"
                  formik={formik}
                />
              </Box>
              <Box key="right" sx={{ p: 1, width: '50%' }}>
                <DateTimeField
                  fieldName="availabilityAt"
                  label="Will be available at date"
                  disablePast
                  formik={formik}
                />
              </Box>
            </Box>
            <Box key="buttons" sx={{ display: 'inline-flex' }}>
              <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
                <Button
                  fullWidth
                  variant="contained"
                  size="small"
                  color="primary"
                  type="submit"
                >
                  Set
                </Button>
              </Box>
              <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
                <Button
                  fullWidth
                  variant="contained"
                  size="small"
                  color="primary"
                  type="button"
                  onClick={handleClose}
                >
                  Cancel
                </Button>
              </Box>
            </Box>
          </Box>
        </form>
        <Loader visible={isLoading} />
      </DialogContent>
    </Dialog>
  );
}
